import React, { useState } from "react";
import InfiniteScroll from "react-infinite-scroll-component";
import { Introduction, Me, Portfolio, TechStack } from "../sections";
import Bottom from "../sections/Bottom";

const LOAD_TIME = 1000;

const Scroll = () => {
  const [items, setItems] = useState([<Me />]);
  const [extraComponents, setRemaining] = useState([
    <Introduction />,
    <Portfolio />,
    <TechStack />,
  ]);
  const [hasMore, setHasMore] = useState(true);

  const fetchMoreData = () => {
    console.log("fetch");
    if (extraComponents.length === 0) {
      setHasMore(false);
      return;
    }

    setTimeout(() => {
      const [next, ...remaining] = extraComponents;
      console.log(`Adding next component: ${items.length}`);
      setItems(items.concat(next));
      setRemaining(remaining);
    }, LOAD_TIME);
  };

  return (
    <InfiniteScroll
      dataLength={items.length}
      next={fetchMoreData}
      hasMore={hasMore}
      loader={<h4>Loading...</h4>} /// TODO replace with spinner
      endMessage={<Bottom />}
    >
      {items.map((i, index) => (
        <div key={index}>{i}</div>
      ))}
    </InfiniteScroll>
  );
};

export default Scroll;
